// import * as React from 'react';
import { useParams } from 'react-router-dom';

import Staff from '@miq/staffjs';
import Form, { useForm, MetaTitleField, MetaSlugField, MetaDescriptionField } from '@miq/formjs';
import { Response } from '@miq/utiljs';
import { useRequest } from '@miq/hookjs';
import { Icons } from '@miq/componentjs';

import { PublishButton, UnPublishButton } from '../components';
import { getImageComponent } from '../Product/views.UpdateTabs';
import { StaffImgUpdateButton, StaffImgAddButton, StaffImgViewSelect, StaffImgDeleteButton } from '@miq/staffjs/';
import { StaffCategory } from './models';
import { CategoryUpdateForm } from './forms';
import { TCategory } from '../types';

const mapData2Values = (data: TCategory = {}): TCategory => {
  return {
    name: data.name || '',
    description: data.description || '',
    is_published: data.is_published || false,

    meta_title: data.meta_title || '',
    meta_slug: data?.meta_slug || '',
    meta_description: data?.meta_description || '',
  };
};

export default function CategoryStaffUpdateView() {
  const { catSlug } = useParams();
  const form = useForm<TCategory>(mapData2Values());

  const { res, updateData } = useRequest(() => StaffCategory({ slug: catSlug }).retrieve(), {
    refreshDeps: [catSlug],
    onSuccess: ({ data }) => {
      document.title = `${data?.name} - Update category`;
      form.setValues(mapData2Values(data));
    },
    onError: ({ response }) => {},
  });

  const r = new Response(res);
  if (!r.isSuccess) return null;

  let instance = StaffCategory(r.data);

  const handleSuccess = ({ data }: any) => updateData(data);

  const { errors } = form;

  return (
    <Staff.View
      title="Modifier cette catégorie"
      back="../"
      requiredPerms={['store.change_category']}
      className="cat-staff-update-view"
    >
      <div className="d-grid grid-md-4">
        <div className="p-md-1">
          <div className="p-1">
            <CoverCard {...{ instance, updateData }} />
          </div>
        </div>

        <div className="span-md-3 span-lg-2">
          <Staff.Section title="Details">
            <CategoryUpdateForm
              context={form}
              instance={instance}
              fields={['name', 'description']}
              onSuccess={handleSuccess}
            >
              <div className="d-flex justify-content-center">
                <Form.Submit value="Sauvegarder" className="btn-primary-3 min-w-25" />
              </div>
            </CategoryUpdateForm>
          </Staff.Section>

          <Staff.Section title="SEO">
            <CategoryUpdateForm
              context={form}
              instance={instance}
              fields={['meta_title', 'meta_description', 'meta_slug']}
              onSuccess={handleSuccess}
            >
              <MetaTitleField error={errors.meta_title} />
              <MetaSlugField error={errors.meta_slug} />
              <MetaDescriptionField error={errors.meta_description} />

              <div className="d-flex justify-content-center">
                <Form.Submit value="Sauvegarder" className="btn-primary-3 min-w-25" />
              </div>
            </CategoryUpdateForm>
          </Staff.Section>
        </div>

        <div className="span-lg-1">
          <div style={{ top: 0, position: 'sticky' }}>
            {instance.is_published ? (
              <UnPublishButton instance={instance} onSuccess={({ data }: any) => updateData(data)}>
                Dépublier cette catégorie
              </UnPublishButton>
            ) : (
              <PublishButton
                instance={instance}
                onSuccess={({ data }: any) => updateData(data)}
                onError={(err: any) => form.handleError(err)}
              >
                Publier cette catégorie
              </PublishButton>
            )}
          </div>
        </div>
      </div>
    </Staff.View>
  );
}

const CoverCard = (props: { instance: ReturnType<typeof StaffCategory>; updateData: (r: any) => void }) => {
  const { updateData, instance } = props;
  const form = useForm({ view: 'src' });

  const img = Staff.Img(instance?.cover_data || {});

  if (!img?.slug)
    return (
      <div className="d-flex align-items-center justify-content-center flex-column text-center border-1 rounded p-3">
        <StaffImgAddButton
          onSuccess={({ data }: any) => {
            instance
              .update({ cover: data.slug })
              .then(({ data }: any) => updateData(data))
              .catch((err: any) => {});
          }}
          label="Ajouter une image"
          className="btn-primary-3 btn-md"
        />
      </div>
    );

  const Image = getImageComponent(form.values.view);
  return (
    <Form context={form} className="p-cover-update-form">
      <Staff.Section
        border
        title={
          <StaffImgDeleteButton
            onSuccess={() => updateData({ cover_data: null, cover: null })}
            instance={img}
            className="btn-danger-3"
          >
            <Icons.Trash />
          </StaffImgDeleteButton>
        }
        actions={<StaffImgViewSelect required name="view" />}
        className="p-image-card"
      >
        <StaffImgUpdateButton
          instance={img}
          field={form.values.view}
          onSuccess={({ data }: any) => updateData({ cover: data.slug, cover_data: data })}
        >
          <Image {...img} />
        </StaffImgUpdateButton>
      </Staff.Section>
    </Form>
  );
};
